body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

a{
  color: #61dafb;
}
a.active_class{
  color: #fff;
  border-bottom: 1px solid red;
}

.searchbar {
  width: 50%;
  height: auto;
  margin: 0 auto;
  text-align: center;
}

.searchbar input {
  margin: 30px 0;
  padding: 5px 10px;
  border-radius: 5px;
  font-family: source-code-prp, Menlco, Consolas, "courier New";
}

.menu_style {
  width: 100%;
  line-height: 60px;
  background-color: #20232a;
  display: flex;
  justify-content: space-around;
}

/*  */
.setStyle3{
  height: 90vh;
  font-size: 8rem;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  text-shadow: 1px 2px 3px #e5cfe5;
  background-color: #3b6978;
  color: #a6dcef;
}
/*  */
.header {
  width: 100%;
  line-height: 100px;
  background-color: #f5ba13;
  display: flex;
  justify-content: start;
  align-items: center;
  padding-left: 50px;
  color: white;
  box-shadow: 5px 3px 15px -5px rgba(0,0,0,0.5);
}

.main_note{
  width: 500px;
  padding: 15px 10px 5px 10px;
  margin: 20px auto;
}

.deleteIcon {
  width: 0.7em;
  height: 0.7em;
}

input {
width: 100%;
line-height: 30px;
background: transparent;
border: none;
outline: none;
font-size: 1.5rem;
margin: 10px 0;
font-weight: bold;

}

.my_notes h1 {
  color: #34495e;
}
.my_notes p {
  color: gray;
}
form {
  min-height: 50px;
  color: #ecf0f1;
  padding: 15px 10px 5px 10px;
  box-shadow: 3px 5px 15px -5px rgba(0,0,0,0.5);
  border-radius: 10px;
  position: relative;
}
textarea {
  width: 100%;
  line-height: 30px;
  background: transparent;
  border: none;
  outline: none;
  font-size: 1rem;
  margin: 10px 0;
  font-weight: bold;
}

.MuiButton-root {
width: 30px;
height: 50px;
position: absolute;
bottom: -28px;
left: 400px;
cursor: pointer;
background-color: #fff !important;
box-shadow: 5px 5px 15px -5px rgba(0, 0, 0, 0.5);
border-radius: 50% !important;
}

.MuiButton-root .plus_sign {
  font-size: 45px !important;
  font-weight: 500;
  color: #f4b400;
  cursor: pointer;
  z-index: 222;
  background: transparent;
}

.MuiButton-root:hover {
  background: #f4b400 !important;
}

.MuiButton-root:hover .plus_sign {
  color: #fff;
  background: transparent;
}

.note {
background: #fff;
border-radius: 7px;
box-shadow: 0 2px 5px #ccc;
padding: 10px;
width: 240px;
margin: 16px;
float: left;
}

.note h1 {
  font-size: 1.1em;
  margin-bottom: 6px;
}

.note p {
  font-size: 1.1em;
  margin-bottom: 10px;
  white-space: pre-wrap;
  word-wrap: break-word;
}

.note .btn {
  position: relative;
  float: right;
  margin-right: 10px;
  color: #f5ba13;
  border: none;
  width: 50px;
  background: transparent;
  cursor: pointer;
  outline: none;
  box-shadow: 3px 3px 15px -10px rgba(0, 0, 0, 0.5);
  border-radius: 50%;
}
.note .btn:hover {
  background-color: #f5ba13;
}

.note.btn:hover .deleteIcon {
  color: #fff;
}

footer {
  width: 100%;
  position: absolute;
  bottom: 0;
}
